import React, { useState, useEffect, useContext } from 'react';
import styled from 'styled-components'
import { db } from '../config/firebaseConfig'
import { SettingContext } from '../context/SettingContext'
import NavBarForAdmin from '../components/NavBarForAdmin'

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

function fillZero(width, str){
  return str.length >= width ? str:new Array(width-str.length+1).join('0')+str;//남는 길이만큼 0으로 채움
}

const AdminPage = () => {
  const [ setting ] = useContext(SettingContext)
  const [ userlist, setUserlist ] = useState(false)
  const [ latestSetting, setLatestSetting ] = useState(null)
  const [ seats, setSeats ] = useState({
    wednesday: 90,
    friday: 90,
    sundayOne: 90,
    sundayTwo: 90,
    sundayThree: 90,
    sundayFour: 90,
    sundaySix: 90,
  })
  const [ comments, setComments ] = useState({
    commentWed: "",
    commentFri: "",
    commentInBox: "",
    commentSunLineOne: "",
    commentSunLineTwo: "",
    commentSunLineThree: "",
    commentOverviewLineOne: "",
    commentOverviewLineTwo: "",
    commentOverviewLineThree: "",
  })

  const loadSettings = () => {
    db.collection('setting').doc('latest').get().then((doc) => {
      if (doc.exists) {
        console.log("Setting data:", doc.data());
        setLatestSetting(doc.data())
      } else {
        console.log("No such document!");
      }
    }).catch((error) => {
      console.log("Error @loadSettings: ", error)
    })
  }

  useEffect(() => {
    console.log('세팅값 불러오는 중')
    if (latestSetting === null) {
      loadSettings()
    }
  }, [latestSetting])



  const batch = db.batch();
  
  let wednesday = new Date();
  wednesday.setDate(wednesday.getDate() + (3 + 7 - wednesday.getDay()) % 7);
  const wednesdayString = `${wednesday.getFullYear()}${fillZero(2, (wednesday.getMonth()+1).toString())}${fillZero(2, (wednesday.getDate()).toString())}`

  let friday = new Date();
  friday.setDate(friday.getDate() + (5 + 7 - friday.getDay()) % 7);
  const fridayString = `${friday.getFullYear()}${fillZero(2, (friday.getMonth()+1).toString())}${fillZero(2, (friday.getDate()).toString())}`

  let sunday = new Date();
  sunday.setDate(sunday.getDate() + (0 + 7 - sunday.getDay()) % 7);
  const sundayString = `${sunday.getFullYear()}${fillZero(2, (sunday.getMonth()+1).toString())}${fillZero(2, (sunday.getDate()).toString())}`

  const onClickNotification = () => {
    const settingRef = db.collection('setting').doc('latest')
    settingRef.update({
      commentOverviewLineOne: comments.commentOverviewLineOne,
      commentOverviewLineTwo: comments.commentOverviewLineTwo,
      commentOverviewLineThree: comments.commentOverviewLineThree,
    })
    .then(() => {
      console.log("Document successfully written!");
      alert('전체공지사항 업데이트 성공했습니다')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert('전체공지사항 업데이트 실패했습니다')
    });
  }

  const onClickWeekdayNotification = () => {
    const settingRef = db.collection('setting').doc('latest')
    settingRef.update({
      commentWed: comments.commentWed,
      commentFri: comments.commentFri,
    })
    .then(() => {
      console.log("Document successfully written!");
      alert('주중예배 공지사항 업데이트 성공했습니다')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert('주중예배 공지사항 업데이트 실패했습니다')
    });
  }

  const onClickSundayNotification = () => {
    const settingRef = db.collection('setting').doc('latest')
    settingRef.update({
      commentInBox: comments.commentInBox,
      commentSunLineOne: comments.commentSunLineOne,
      commentSunLineTwo: comments.commentSunLineTwo,
      commentSunLineThree: comments.commentSunLineThree,
    })
    .then(() => {
      console.log("Document successfully written!");
      alert('주일예배 공지사항 업데이트 성공했습니다')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert('주일예배 공지사항 업데이트 실패했습니다')
    });
  }

  const onClickWeekday = () => {
      const wedRef = db.collection('수요예배').doc('1부').collection(wednesdayString).doc('--stats--')
      const friRef = db.collection('금요성령집회').doc('1부').collection(fridayString).doc('--stats--')
      const settingRef = db.collection('setting').doc('latest')
      batch.set(wedRef, { ReservationCount: 0 })
      batch.set(friRef, { ReservationCount: 0 })
      batch.update(settingRef, {
        isWeekday: true,
        wednesday: wednesdayString,
        friday: fridayString,
        TimerWednesDay: [wednesday.getFullYear(), wednesday.getMonth(), wednesday.getDate()-1, 19, 0],
        TimerFriDay: [friday.getFullYear(), friday.getMonth(), friday.getDate()-3, 20, 0],
        TimerSunDay: "",
        closeWednesDay: false,
        closeFriDay: false,
        closeSunDay: false,
        wednesDaySeats: parseInt(seats.wednesday),
        fridaySeats: parseInt(seats.friday),
        sundayOneSeats: "",
        sundayTwoSeats: "",
        sundayThreeSeats: "",
        sundayFourSeats: "",
        sundaySixSeats: "",
        commentSunLineOne: "",
        commentSunLineTwo: "",
        commentSunLineThree: "",

      })
      batch.commit()
      .then(() => {
        console.log("Document successfully written!");
        alert('업데이트 성공했습니다')
      })
      .catch((error) => {
        console.error("Error writing document: ", error);
        alert('업데이트 실패했습니다')
      });
  }


  const onClickSunDay = () => {
    const firstRef = db.collection('주일예배').doc('1부').collection(sundayString).doc('--stats--')
    const secondRef = db.collection('주일예배').doc('2부').collection(sundayString).doc('--stats--')
    const thirdRef = db.collection('주일예배').doc('3부').collection(sundayString).doc('--stats--')
    const forthRef = db.collection('주일예배').doc('4부').collection(sundayString).doc('--stats--')
    const sixthRef = db.collection('주일예배').doc('6부').collection(sundayString).doc('--stats--')
    const settingRef = db.collection('setting').doc('latest')
    batch.set(firstRef, { ReservationCount: 0 })
    batch.set(secondRef, { ReservationCount: 0 })
    batch.set(thirdRef, { ReservationCount: 0 })
    batch.set(forthRef, { ReservationCount: 0 })
    batch.set(sixthRef, { ReservationCount: 0 })
    batch.update(settingRef, {
      isWeekday: false,
      sunday: sundayString,
      TimerWednesDay: "",
      TimerFriDay: "",
      TimerSunDay: [sunday.getFullYear(), sunday.getMonth(), sunday.getDate()-3, 19, 0],
      closeWednesDay: false,
      closeFriDay: false,
      closeSunDay: false,
      wednesDaySeats: "",
      fridaySeats: "",
      sundayOneSeats: parseInt(seats.sundayOne),
      sundayTwoSeats: parseInt(seats.sundayTwo),
      sundayThreeSeats: parseInt(seats.sundayThree),
      sundayFourSeats: parseInt(seats.sundayFour),
      sundaySixSeats: parseInt(seats.sundaySix),
      commentWed: "",
      commentFri: "",
    })
    batch.commit()
    .then(() => {
      console.log("Document successfully written!");
      alert('업데이트 성공했습니다')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert('업데이트 실패했습니다')
    });
  }

  const onClickLookUp = (title, time, date) => {
    db.collection(title).doc(time).collection(date).orderBy("serverTime").get().then((querySnapshot) => {
      let tempArray = []
       querySnapshot.forEach((doc) => {
         tempArray.push({
          id: doc.id,
          ...doc.data()
         })
      })
      setUserlist(tempArray)
    }).catch(error => {
      console.log(error)
    })
  }

  const converTime = (timestamp) => {
    let date = new Date(timestamp * 1000)
    let hours = date.getHours();
    let minutes = "0" + date.getMinutes();
    let seconds = "0" + date.getSeconds();
    let formattedTime = hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
    return formattedTime
  }

  const onForingCloseClick = (days) => {
    const settingRef = db.collection('setting').doc('latest')
    if (days === "수요일") {
      settingRef.update({
        closeWednesDay: true
      })
      .then(() => {
        console.log("Close Wednesday Service successfully!");
        alert('수요예배 신청을 마감하겠습니다.')
      })
    } else if (days === "금요일") {
      settingRef.update({
        closeFriDay: true
      })
      .then(() => {
        console.log("Close Friday Service successfully!");
        alert('금요성령집회 신청을 마감하겠습니다.')
      })
    } else if (days === "주일") {
      settingRef.update({
        closeSunDay: true
      })
      .then(() => {
        console.log("Close Sunday Service successfully!");
        alert('주일예배 신청을 마감하겠습니다.')
      })
    }
  }

  const onSeatsChange = (e) => {
    const { value, name } = e.target
    setSeats({
      ...seats,
      [name]: value
    })
  }

  const onCommentChange = (e) => {
    const { value, name } = e.target
    setComments({
      ...comments,
      [name]: value
    })
  }


  return (
    <Container>
      <NavBarForAdmin />

      <div style={{fontSize: 16, fontWeight: 600, marginBottom: 24, marginTop: 24, backgroundColor:'lightgray', padding: 10}}>현재 세팅 상태</div>
        <div>{latestSetting==null ? "" : latestSetting.isWeekday ? "현재 주중예배(수/금)가 세팅되어 있습니다." : "주일예배가 세팅되어 있습니다."}</div>


      <div style={{fontSize: 16, fontWeight: 600, marginBottom: 24, marginTop: 24, backgroundColor:'lightgray', padding: 10}}>전체공지사항</div>
      <div style={{display:'flex', flexDirection:'row', alignItems: 'center'}}>
        <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: '15%', alignItems: 'center'}}>전체공지사항</div>
        <div style={{display:'flex', flexDirection:'column', alignItems: 'center', width: '60%', marginRight: 10}}>
          <input 
            name="commentOverviewLineOne" 
            value={comments.commentOverviewLineOne} 
            onChange={onCommentChange}
            placeholder="공지사항 첫번째 줄" 
            style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
          />
          <input 
            name="commentOverviewLineTwo" 
            value={comments.commentOverviewLineTwo} 
            onChange={onCommentChange} 
            placeholder="공지사항 두번째 줄" 
            style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
          />
          <input 
            name="commentOverviewLineThree" 
            value={comments.commentOverviewLineThree} 
            onChange={onCommentChange} 
            placeholder="공지사항 세번째 줄" 
            style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
          />
        </div>
        <button 
          style={{backgroundColor: '#57FFAD', border: '2px solid black', padding: 10, font: 20, fontWeight: 600}}
          onClick={() => onClickNotification()}
        >
          공지사항 업데이트
        </button>
      </div>
      

      <div style={{fontSize: 16, fontWeight: 600, marginBottom: 24, marginTop: 24, backgroundColor:'lightgray', padding: 10}}>다음 신청 세팅하기</div>
      <SettingContainer>
        <div>주중예배세팅</div>
        <Setting>
          <div>수요예배</div>
          <Title>날짜 : {wednesday.getFullYear()}년 {wednesday.getMonth()+1}월 {wednesday.getDate()}일</Title>
          <Title>타이머시간 : {`${wednesday.getFullYear()}년 ${wednesday.getMonth()+1}월 ${wednesday.getDate()-1}일 19시`}</Title>
          <Title>좌석수 : {seats.wednesday}</Title>
        </Setting>
        <Setting>
          <div>금요예배</div>
          <Title>날짜 : {friday.getFullYear()}년 {friday.getMonth()+1}월 {friday.getDate()}일</Title>
          <Title>타이머시간 : {`${friday.getFullYear()}년 ${friday.getMonth()+1}월 ${friday.getDate()-3}일 20시`}</Title>
          <Title>좌석수 : {seats.friday}</Title>
        </Setting>
        <div style={{borderTop: '1px solid black', marginBottom: 10}}/>
        <div>
          <div style={{display: 'flex', flexDirection: 'row', marginBottom: 10, alignItems: 'center'}}>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>수요예배 좌석조정</div>
            <input name="wednesday" value={seats.wednesday} onChange={onSeatsChange} style={{width: 50}}/>
            <div style={{marginLeft: 10, marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>금요예배 좌석조정</div>
            <input name="friday" value={seats.friday} onChange={onSeatsChange} style={{width: 50, marginRight: 30}}/>
          </div>
          <button 
            style={{backgroundColor: '#4FDAFF', border: '2px solid #222', padding: 10, font: 20, fontWeight: 600}}
            onClick={() => onClickWeekday()}
          >
            주중예배 좌석수 및 타이머 세팅
          </button>

          <div style={{display:'flex', marginTop: 20, marginBottom: 10, flexDirection:'row', alignItems: 'center'}}>
            <div style={{display:'flex', flexDirection:'column', width: '75%', marginRight: 10}}>
              <div style={{display:'flex', flexDirection:'row', marginBottom: 10}}>
                <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: '20%', alignItems: 'center', justifyContent: 'center'}}>
                  <span>수요예배</span>
                  <span>공지사항</span>
                </div>
                <input 
                  name="commentWed" 
                  value={comments.commentWed} 
                  onChange={onCommentChange} 
                  placeholder="수요예배 공지사항 1줄만"
                  style={{width: '80%', height: '1.5rem'}}
                />
              </div>
              <div style={{display:'flex', flexDirection:'row'}}>
                <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: '20%', alignItems: 'center', justifyContent: 'center'}}>
                  <span>금요예배</span>
                  <span>공지사항</span>
                </div>
                <input 
                  name="commentFri" 
                  value={comments.commentFri} 
                  onChange={onCommentChange} 
                  placeholder="금요성령집회 공지사항 1줄만"
                  style={{width: '80%', height: '1.5rem'}}
                />
              </div>
            </div>
            <div>
              <button 
                style={{
                  backgroundColor: '#57FFAD', border: '2px solid black', padding: 10, font: 20, fontWeight: 600,
                }}
                onClick={() => onClickWeekdayNotification()}
              >
                공지사항 업데이트
              </button>
            </div>
          </div>

          <div 
            style={{ border: '2px solid red', padding: 10, marginTop: 10}}
          >
            <button 
              onClick={() => onForingCloseClick('수요일')}
              style={{ padding: 10, font: 20, fontWeight: 600, marginRight: 20, backgroundColor: '#F54F50', color: '#fff'}}
            >
              수요예배 신청마감
            </button>
            <button 
              onClick={() => onForingCloseClick('금요일')}
              style={{ padding: 10, font: 20, fontWeight: 600, marginRight: 20, backgroundColor: '#F54F50', color: '#fff'}}
            >
              금요성령집회 신청마감
            </button>
          </div>

        </div>
      </SettingContainer>
      <div style={{borderTop: '2px solid black', marginBottom: 10}}/>
      <SettingContainer>
        <div>주일예배세팅</div>
        <Setting>
          <Title>날짜 : {sunday.getFullYear()}년 {sunday.getMonth()+1}월 {sunday.getDate()}일</Title>
          <Title>타이머시간 : {`${sunday.getFullYear()}년 ${sunday.getMonth()+1}월 ${sunday.getDate()-3}일 19시`}</Title>
        </Setting>
        <Setting>
          <Title>1부 좌석수 : {seats.sundayOne}</Title>
          <Title>2부 좌석수 : {seats.sundayTwo}</Title>
          <Title>3부 좌석수 : {seats.sundayThree}</Title>
          <Title>4부 좌석수 : {seats.sundayFour}</Title>
          <Title>6부 좌석수 : {seats.sundaySix}</Title>
        </Setting>
        <div style={{borderTop: '1px solid black', marginBottom: 10}} />
        <div>
          <div style={{display: 'flex', flexDirection: 'row', marginBottom: 10, alignItems: 'center'}}>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>1부예배 좌석조정</div>
            <input name="sundayOne" value={seats.sundayOne} onChange={onSeatsChange}  style={{width: 30, marginRight: 10}}/>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>2부예배 좌석조정</div>
            <input name="sundayTwo" value={seats.sundayTwo} onChange={onSeatsChange}  style={{width: 30, marginRight: 10}}/>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>3부예배 좌석조정</div>
            <input name="sundayThree" value={seats.sundayThree} onChange={onSeatsChange}  style={{width: 30, marginRight: 10}}/>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>4부예배 좌석조정</div>
            <input name="sundayFour" value={seats.sundayFour} onChange={onSeatsChange} style={{width: 30, marginRight: 10}}/>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>6부예배 좌석조정</div>
            <input name="sundaySix" value={seats.sundaySix} onChange={onSeatsChange} style={{width: 30, marginRight: 10}}/>
          </div>

          <button 
            style={{backgroundColor: '#4FDAFF', border: '2px solid black', padding: 10, font: 20, fontWeight: 600}}
            onClick={() => onClickSunDay()}
          >
            주일예배 좌석수 및 타이머 세팅
          </button>

          <div style={{display:'flex', flexDirection:'row', alignItems: 'center', marginTop: 20}}>
            <div style={{marginRight: 10, display: 'flex', flexDirection:'column', width: '15%', alignItems: 'center'}}>
              <span>주일예배</span>
              <span>공지사항</span>
            </div>
            <div style={{display:'flex', flexDirection:'column', alignItems: 'center', width: '60%', marginRight: 10}}>
            <input 
                name="commentInBox" 
                value={comments.commentInBox} 
                onChange={onCommentChange} 
                placeholder="박스 안 공지사항"
                style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
              />
              <input 
                name="commentSunLineOne" 
                value={comments.commentSunLineOne} 
                onChange={onCommentChange} 
                placeholder="공지사항 첫번째 줄"
                style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
              />
              <input 
                name="commentSunLineTwo" 
                value={comments.commentSunLineTwo} 
                onChange={onCommentChange} 
                placeholder="공지사항 두번째 줄"
                style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
              />
              <input 
                name="commentSunLineThree" 
                value={comments.commentSunLineThree} 
                onChange={onCommentChange} 
                placeholder="공지사항 세번째 줄"
                style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
              />
            </div>
            <button 
              style={{
                backgroundColor: '#57FFAD', border: '2px solid black', padding: 10, font: 20, fontWeight: 600,
              }}
              onClick={() => onClickSundayNotification()}
            >
              공지사항 업데이트
            </button>
          </div>

          <div 
            style={{ border: '2px solid red', padding: 10, marginTop: 10}}
          >
            <button 
              onClick={() => onForingCloseClick('주일')}
              style={{ padding: 10, font: 20, fontWeight: 600, marginRight: 20, backgroundColor: '#F54F50', color: '#fff'}}
            >
              주일예배 신청마감
            </button>
          </div>

        </div>
      </SettingContainer>

      <div style={{fontSize: 16, fontWeight: 600, marginBottom: 28, marginTop: 28, backgroundColor:'lightgray', padding: 10}}>신청자 명단 확인</div>
      
      <LookupContainer>
        <LookupButton onClick={() => onClickLookUp('수요예배', '1부', wednesdayString)}>수요예배</LookupButton>
        <LookupButton onClick={() => onClickLookUp('금요성령집회', '1부', fridayString)}>금요예배</LookupButton>
        <LookupButton onClick={() => onClickLookUp('주일예배', '1부', sundayString)}>1부조회</LookupButton>
        <LookupButton onClick={() => onClickLookUp('주일예배', '2부', sundayString)}>2부조회</LookupButton>
        <LookupButton onClick={() => onClickLookUp('주일예배', '3부', sundayString)}>3부조회</LookupButton>
        <LookupButton onClick={() => onClickLookUp('주일예배', '4부', sundayString)}>4부조회</LookupButton>
        <LookupButton onClick={() => onClickLookUp('주일예배', '6부', sundayString)}>6부조회</LookupButton>
      </LookupContainer>
      

      <TableContainer component={Paper}>
        <Table aria-label="simple table" style={{minWidth: 250}}>
          <TableHead>
            <TableRow>
              <TableCell align="center">순서</TableCell>
              <TableCell align="center">이름</TableCell>
              <TableCell align="center">직분</TableCell>
              <TableCell align="center">소속(선교회)</TableCell>
              <TableCell align="center">시간</TableCell>
              <TableCell align="center">장소</TableCell>
            </TableRow>
          </TableHead>
          {userlist && (
            <>
            {userlist.map((user, index) => (
              <TableBody>
                <TableRow key={index}>
                  <TableCell align="center">{user.index}</TableCell>
                  <TableCell align="center">{user.name}</TableCell>
                  <TableCell align="center">{user.position}</TableCell>
                  <TableCell align="center">{user.division}</TableCell>
                  <TableCell align="center">{converTime(user.serverTime)}</TableCell>
                  <TableCell align="center">
                    {user.index <= setting.sundayFourSeats-20 ? '본당' : user.index >= setting.sundayFourSeats-20+1 && user.index < setting.sundayFourSeats+1 ? '베다니홀' : '실패' }
                  </TableCell>
                </TableRow>
              </TableBody>
            ))}  
            </>
          )}
        </Table>
      </TableContainer>
    </Container>
    
  )
}

