import React from 'react'
import { BrowserRouter, Switch, Route  } from "react-router-dom";
import styled, { createGlobalStyle } from "styled-components";

import { SettingProvider } from './context/SettingContext'
import { UserProvider } from './context/UserContext'

// import NavBar from './components/NavBar'
import LoadingPage from './pages/LoadingPage'
import LandingPage from './pages/LandingPage'
import SundayService from './pages/services/SundayService'
import WednesdayService from './pages/services/WednesdayService'
import FridayService from './pages/services/FridayService'
// import Resultpage from './pages/ResultPage'
import AdminPage from './pages/AdminPage'
import AuthPage from './pages/AuthPage'
import SelfCheckPage from './pages/SelfCheckPage'
import SendingPage from './pages/SendingPage'

function App() {

  return (
    <SettingProvider>
      <UserProvider>
        <BrowserRouter>
          <GlobalStyle />
          <Wrapper>
            <Container>
              <Switch>
                
                <Route exact path={'/main'} component={LandingPage} />
                <Route exact path={'/wednesday'} component={WednesdayService} />
                <Route exact path={'/friday'} component={FridayService} />
                <Route exact path={'/sunday'} component={SundayService} />
                <Route exact path={'/result'} component={SendingPage} />
                <Route exact path={'/auth'} component={AuthPage} />
                <Route exact path={'/admin'} component={AdminPage} />
                <Route exact path={'/check'} component={SelfCheckPage} />
                <Route path={'/'} component={LoadingPage} />
              </Switch>
            </Container>
          </Wrapper>
        </BrowserRouter>
      </UserProvider>
    </SettingProvider>

  );
}

const GlobalStyle = createGlobalStyle`
  body {
    margin: 0;
    padding: 0; 
    box-sizing: border-box;
  }
`;

  const Wrapper = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: scroll;
  `;

  const Container = styled.div`
    width: 100%;
    max-width: 585px;
    display: flex;
    flex-direction: column;
  `;





export default App;
