import React, { useState, createContext } from 'react'

const SettingContext = createContext([{}, ()=>{}])

const SettingProvider = (props) => {
  const [state, setState] = useState({
    init: false,
    isWeekday: true,
    wednesday: "",
    friday: "",
    sunday: "",
    timerWednesDay: "",
    timerFriDay: "",
    timerSunDay: "",
    closeWednesDay: false,
    closeFriDay: false,
    closeSunDay: false,
    wednesDaySeats: 90,
    fridaySeats: 90,
    sundayOneSeats: 90,
    sundayTwoSeats: 90,
    sundayThreeSeats: 90,
    sundayFourSeats: 90,
    sundaySixSeats: 90,
    commentOverviewLineOne: "",
    commentOverviewLineTwo: "",
    commentOverviewLineThree: "",
    commentWed: "",
    commentFri: "",
    commentInBox: "",
    commentSunLineOne: "",
    commentSunLineTwo: "",
    commentSunLineThree: "",
    isSubmitting: false,
    isSubmitted: false,
  })

  return <SettingContext.Provider value={[state, setState]}>{props.children}</SettingContext.Provider>
}

export { SettingContext, SettingProvider }