import React, {useContext, useEffect} from 'react';
import { useHistory } from 'react-router-dom';
import { SettingContext } from '../context/SettingContext'
import { db } from '../config/firebaseConfig'
import styled from 'styled-components'

const LoadingPage = () => {
  const [ setting, setSetting ] = useContext(SettingContext)
  const history = useHistory();

  const initialize = async () => {
    const settingRef = db.collection('setting').doc('latest')
    await settingRef.get().then((doc) => {
      if (doc.exists) {
        setSetting({
          ...setting,
          init: true,
          isWeekday: doc.data().isWeekday,
          wednesday: doc.data().wednesday,
          friday: doc.data().friday,
          sunday: doc.data().sunday,
          timerWednesDay: doc.data().TimerWednesDay,
          timerFriDay: doc.data().TimerFriDay,
          timerSunDay: doc.data().TimerSunDay,
          closeWednesDay: doc.data().closeWednesDay,
          closeFriDay: doc.data().closeFriDay,
          closeSunDay: doc.data().closeSunDay,
          wednesDaySeats: doc.data().wednesDaySeats,
          fridaySeats: doc.data().fridaySeats,
          sundayOneSeats: doc.data().sundayOneSeats,
          sundayTwoSeats: doc.data().sundayTwoSeats,
          sundayThreeSeats: doc.data().sundayThreeSeats,
          sundayFourSeats: doc.data().sundayFourSeats,
          sundaySixSeats: doc.data().sundaySixSeats,
          commentOverviewLineOne: doc.data().commentOverviewLineOne,
          commentOverviewLineTwo: doc.data().commentOverviewLineTwo,
          commentOverviewLineThree: doc.data().commentOverviewLineThree,
          commentWed: doc.data().commentWed,
          commentFri: doc.data().commentFri,
          commentInBox: doc.data().commentInBox,
          commentSunLineOne: doc.data().commentSunLineOne,
          commentSunLineTwo: doc.data().commentSunLineTwo,
          commentSunLineThree: doc.data().commentSunLineThree,
        })
        console.log("세팅업데이트 성공");
      } else {
        console.log("데이터가 없습니다.")
      }
    }).catch((error) => {
      console.log("Errors @initialize: ", error)
    })
  }

  useEffect(() => {
    console.log('무한루프체크')
    initialize()
    .then(()=>{
      history.push({
        pathname: "/main"
      })
    })
  }, [])

  console.log(setting)

  return (
    <Container>
      <div>화양교회 예배신청 페이지입니다</div>
      <div>잠시만 기다려 주세요</div>
    </Container>
  )
}

const Container = styled.div`
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

export default LoadingPage;