import React, { useState, useContext } from 'react';
import { useForm, Controller } from "react-hook-form";
import styled from 'styled-components'
import { SettingContext } from '../context/SettingContext'

import NavBarForCheck from '../components/NavBarForCheck'
import { Radio, RadioGroup, FormControlLabel} from '@material-ui/core';
import NotificationsIcon from '@material-ui/icons/Notifications';

import { db } from '../config/firebaseConfig'

const SelfCheckPage = () => {
  const [ setting ] = useContext(SettingContext)
  const [ userInfo, setUserInfo ] = useState({
    name: "",
    position: "",
    title: "",
    serviceTime: "",
    reservationDate: "",
    serviceSchedule: "",
  })
  const [ open, setOpen ] = useState(false)
  const [ result, setResult ] = useState(null)
  const { register, handleSubmit, errors, control } = useForm()

  const onSubmit = (data) => {
    const { name, position, worship } = data
    let title = ""
    let serviceTime = ""
    let reservationDate = ""
    let serviceSchedule = ""
    if (worship.substring(0,4) === '주일예배') {
      reservationDate = setting.sunday
      if (worship === '주일예배1부') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
        serviceSchedule = '오전 7시 10분'
      } else if (worship === '주일예배2부') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
        serviceSchedule = '오전 8시 00분'
      } else if (worship === '주일예배3부') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
        serviceSchedule = '오전 9시 30분'
      } else if (worship === '주일예배4부') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
        serviceSchedule = '오전 11시 00분'
      } else if (worship === '주일예배6부') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
        serviceSchedule = '오후 4시 00분'
      }
    } else {
      title = worship
      serviceTime = '1부'
      if (worship === '수요예배') {
        reservationDate = setting.wednesday
        serviceSchedule = '저녁 7시 30분'
      } else if (worship === '금요성령집회') {
        reservationDate = setting.friday
        serviceSchedule = '저녁 8시 00분'
      } else {
        reservationDate = setting.sunday
      }
    }

    setUserInfo({
      name: name.trim(),
      position: position.trim(),
      title: title,
      serviceTime: serviceTime,
      reservationDate: reservationDate,
      serviceSchedule: serviceSchedule
    })
    checkResult( name.trim(), position.trim(), title, serviceTime, reservationDate )
    setOpen(true)
  }

  const onCloseClick = () => {
    setResult(null)
    setOpen(false)
  }

  const checkResult = ( name, position, title, serviceTime, reservationDate ) => {
    const userListRef = db.collection(title).doc(serviceTime).collection(reservationDate)
    let indexQuery = userListRef.where("index", "==", setting.sundayFourSeats)
    let query = userListRef.where("name", "==", name).where("position", "==", position);
    query.get().then(querySnapshot => {
      if (querySnapshot.empty) {
        console.log('DB에 없음')
        setResult('실패')
      } else {
        querySnapshot.forEach((doc) => {
          if (doc.data().index <= (setting.sundayFourSeats-20)) {
            console.log('본당 당첨')
            setResult('본당')
          } else if (doc.data().index >= (setting.sundayFourSeats-20+1) && doc.data().index < (setting.sundayFourSeats+1)) {
            if (doc.data().index === (setting.sundayFourSeats)) {
              indexQuery.get().then((querySnapshot) => {
                let tempArray = []
                querySnapshot.forEach((doc) => {
                  tempArray.push({
                   id: doc.id,
                   ...doc.data()
                  })
               })
               tempArray.sort((a, b) => {
                 return a.serverTime - b.serverTime
               })
               if (tempArray[0].name === name) {
                console.log('마지막 선착순 당첨')
                setResult('베다니홀')
               } else {
                console.log('제출시간에서 밀림')
                setResult('실패')
               }
              })
            } else {
              console.log('베다니홀 당첨')
              setResult('베다니홀')
            }
          } else {
            console.log('선착순에서 실패')
            setResult('실패')
          }
        })
      }
    })
  }

  return (
    <Container>
      <NavBarForCheck />
      <Header>신청결과확인 페이지</Header>

      <CommentContainer>
        <CommentHeader>
          <NotificationsIcon style={{ marginRight: '0.5rem'}}/>
          <div>주의사항</div>
        </CommentHeader>
        <CommentBody>
          <div style={{ marginBottom: '0.25rem' }}>이름과 직분을 정확하게 입력해주세요</div>
          <div>신청시 이름과 직분에 오타가 있었을 경우</div>
          <div>확인되지 않을 수 있습니다.</div>
        </CommentBody>
      </CommentContainer>

      {!open ? (
        <InserForm onSubmit={handleSubmit(onSubmit)} style={{display:'flex', flexDirection:'column'}}>
          <InputContainer>
            <Title>이름</Title>
            <Input name="name" placeholder="이름을 입력해주세요" ref={register({ required: true, maxLength: 4 })}/>
            {errors.name && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}>본인 한사람 이름만 입력해주세요</div>}
          </InputContainer>
          <InputContainer>
            <Title>직분</Title>
            <Input name="position" placeholder="장로/권사/집사/성도/청년/학생" ref={register({ required: true})} />
            {errors.position && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}>직분을 입력해주세요</div>}
          </InputContainer>
          <InputContainer>
            <Title>확인하실 예배를 선택하세요</Title>
            <Controller
              as={
                <RadioGroup aria-label="예배" style={{paddingLeft: '1rem'}}>
                  <div style={{display: 'flex', flexDirection: 'row'}}>
                    <FormControlLabel
                      value="수요예배"
                      control={<Radio />}
                      label="수요예배"
                    />
                    <FormControlLabel
                      value="금요성령집회"
                      control={<Radio />}
                      label="금요성령집회"
                    />
                  </div>
                  <div style={{display: 'flex', flexDirection: 'row', flexWrap:'wrap'}}>
                    <FormControlLabel
                      value="주일예배1부"
                      control={<Radio />}
                      label="주일예배1부"
                    />
                    <FormControlLabel
                      value="주일예배2부"
                      control={<Radio />}
                      label="주일예배2부"
                    />
                    <FormControlLabel
                      value="주일예배3부"
                      control={<Radio />}
                      label="주일예배3부"
                    />
                    <FormControlLabel
                      value="주일예배4부"
                      control={<Radio />}
                      label="주일예배4부"
                    />
                    <FormControlLabel
                      value="주일예배6부"
                      control={<Radio />}
                      label="주일예배6부"
                    />
                  </div>
                </RadioGroup>
              }
              name="worship"
              control={control}
              defaultValue={"주일예배"}
              rules={{ required: true }}
            />
            {errors.worship && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}>예배를 선택해주세요</div>}
          </InputContainer>
          <InputContainer>
            <SubmitButton type="submit" disabled={setting.isSubmitting}>신청결과확인</SubmitButton>
          </InputContainer>
        </InserForm>

      ) : (
        <ResultContainer result={result}>
        {result == null ? (
          <div>결과 확인 중...</div>
        ) : (
          <>
          {result === '실패' ? (
            <>
            <div style={{fontSize: '1.5rem', marginBottom: '0.5rem'}}>{userInfo.name} {userInfo.position}님 </div>
            <div style={{fontSize: '1.5rem', marginBottom: '1rem'}}>죄송합니다.</div>
            {userInfo.title === '주일예배' ? (
              <>
                <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: '#F54E50'}}>{userInfo.title} {userInfo.serviceTime}</div>
                <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: '#F54E50'}}>신청이 안되셨습니다.</div>
              </>
            ) : (
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: '#F54E50'}}>{userInfo.title} 신청이 안되셨습니다.</div>
            )}
            </>
          ) : (
            <>
            <div style={{fontSize: '1.5rem', marginBottom: '1rem'}}>{userInfo.name} {userInfo.position}님 </div>
            {userInfo.title === '주일예배' ? (
              <>
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: handleColorType(result)}}>{userInfo.title} {userInfo.serviceTime}</div>
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: handleColorType(result)}}>{result}{result === '본당' ? '으로': '로'} 신청되셨습니다.</div>
              </>
            ) : (
              <>
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: handleColorType(result)}}>{userInfo.title}</div>
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: handleColorType(result)}}>{result}{result === '본당' ? '으로': '로'} 신청되셨습니다.</div>
              </>
            )}
            <div style={{fontSize: '1.25rem', marginBottom: '2rem'}}>많은 은혜 받으시길 바랍니다</div>
            </>
          )}
          <div style={{display: 'flex', flexDirection: 'row'}}>
            <div style={{marginRight: 15}}>일시 : {userInfo.reservationDate.substring(0,4)+'.'+userInfo.reservationDate.substring(4,6)+'.'+userInfo.reservationDate.substring(6,8)+'.'}</div>
            <div>시간 : {userInfo.serviceSchedule}</div>
          </div>
          <ReturnButton 
            onClick={() => onCloseClick()}
          >
            결과 확인 페이지로
          </ReturnButton>
          </>
        )}
        </ResultContainer>
      )}
    </Container>
  )
}



const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const Header = styled.div`
  display: flex;
  justify-content: center;
  margin: 1.5rem 0 0;
  font-size: 2rem;
  font-weight: 700;
`;

const CommentContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 2rem 0 1.25rem;
`;

const CommentHeader = styled.div`
  display: flex;
  width: 100%;
  background-color: #F2B49B;
  align-items: center;
  color: #fff;
  padding: 0.5rem;
  box-sizing: border-box;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
`;

const CommentBody = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 0.75rem;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0.25px 1px gray;
`

const InserForm = styled.form`
  padding: 1rem;
  width: 80%;
  display: flex;
  justify-content: center;
`;

const InputContainer = styled.section`
  display: flex;
  flex-direction: column;
  margin: 1rem 0;
`;

const Title = styled.label`
  width: 100%;
  margin-bottom: 0.4rem;
  font-size: 1rem;
  color: #222;
`;

const Input = styled.input`
  width: 100%;
  border-radius: 6px;
  border: 2px solid #228be6;
  padding: 0.75rem;
  outline: none;
  font-size: 1rem;
  box-sizing: border-box;
`;

const SubmitButton = styled.button`
  width: 100%;
  padding: 0.75rem;
  margin: 0.75rem 0;
  color: #fff;
  background-color: #228be6;
  border: 2px solid #228be6;
  border-radius: 4px;
  outline: none;
  box-sizing: border-box;
  font-size: 1rem;
  font-weight: 600;
`;

const ResultContainer = styled.div`
  width: 85%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 16px;
  padding: 1rem;
  margin-top: 2rem;
  border: 2px solid ${props => handleColorType(props.result)};
  box-sizing: border-box;
`;

const ReturnButton = styled.button`
  padding: 0.75rem;
  border-radius: 8px;
  font-size: 1.1rem;
  margin-top: 1.25rem;
`;

const handleColorType = result => {
    switch (result){
      case '본당':
        return "#4FDAFF";
      case '베다니홀':
        return "#57FFAD";
      case '실패':
        return "#F54E50";
      default:
        return "#222222";
    }
  }

export default SelfCheckPage;