import React, { useContext, useEffect, useState } from 'react';
import styled from 'styled-components'
import { SettingContext } from '../context/SettingContext'
import { db } from '../config/firebaseConfig'

import NavBar from '../components/NavBar'
import Timer from '../components/Timer'
import WeekdayServiceButton from '../components/WeekdayServiceButton'
import SundayServiceButton from '../components/SundayServiceButton'
// import Footer from '../components/Footer'

import NotificationsIcon from '@material-ui/icons/Notifications';

const LandingPage = () => {
  const [ setting ] = useContext(SettingContext)
  const [ openWednesDay, setOpenWednesDay ] = useState(false)
  const [ openFriDay, setOpenFriDay ] = useState(false)
  const [ openSunDay, setOpenSunDay ] = useState(false)
  const [ remainingWednesDay, setRemainingWednesDay ] = useState(0)
  const [ remainingFriDay, setRemainingFriDay ] = useState(0)
  const [ remainingSunDayFirst, setRemainingSunDayFirst ] = useState(0)
  const [ remainingSunDaySecond, setRemainingSunDaySecond ] = useState(0)
  const [ remainingSunDayThird, setRemainingSunDayThird ] = useState(0)
  const [ remainingSunDayFourth, setRemainingSunDayFourth ] = useState(0)
  const [ remainingSunDaySixth, setRemainingSunDaySixth ] = useState(0)

  useEffect(() => {
    if (setting.isWeekday) {
      db.collection("수요예배").doc('1부').collection(setting.wednesday).doc('--stats--').onSnapshot((doc) => {
        const wednestempCount = doc.data().ReservationCount
        setRemainingWednesDay(wednestempCount)
      }, (error) => {
        console.log("Error @ListenDB: ", error)
      })
    } 
    return () => {
      console.log('수요예배 리스닝 분리');
    };
  }, [setting.isWeekday, setting.wednesday])

  useEffect(() => {
    if (setting.isWeekday) {
      db.collection("금요성령집회").doc('1부').collection(setting.friday).doc('--stats--').onSnapshot((doc) => {
        const fridaytempCount = doc.data().ReservationCount
        setRemainingFriDay(fridaytempCount)
      }, (error) => {
        console.log("Error @ListenDB: ", error)
      })
    } 
    return () => {
      console.log('금요예배 리스닝 분리');
    };
  }, [setting.isWeekday, setting.friday])

  useEffect(() => {
    if (!setting.isWeekday) {
      db.collection("주일예배").doc('1부').collection(setting.sunday).doc('--stats--').onSnapshot((doc) => {
        const firsttempCount = doc.data().ReservationCount
        setRemainingSunDayFirst(firsttempCount)
      }, (error) => {
        console.log("Error @ListenDB: ", error)
      })
    } 
    return () => {
      console.log('주일예배 1부 리스닝 분리')
    }

  }, [setting.isWeekday, setting.sunday])

  useEffect(() => {
    if (!setting.isWeekday) {
      db.collection("주일예배").doc('2부').collection(setting.sunday).doc('--stats--').onSnapshot((doc) => {
        const secondtempCount = doc.data().ReservationCount
        setRemainingSunDaySecond(secondtempCount)
      }, (error) => {
        console.log("Error @ListenDB: ", error)
      })
    } 
    return () =>  {
      console.log('주일예배 2부 리스닝 분리')
    }

  }, [setting.isWeekday, setting.sunday])
  
  useEffect(() => {
    if (!setting.isWeekday) {
      db.collection("주일예배").doc('3부').collection(setting.sunday).doc('--stats--').onSnapshot((doc) => {
        const thirdtempCount = doc.data().ReservationCount
        setRemainingSunDayThird(thirdtempCount)
      }, (error) => {
        console.log("Error @ListenDB: ", error)
      })
    } 
    return () => {
      console.log('주일예배 3부 리스닝 분리')
    }

  }, [setting.isWeekday, setting.sunday])

  useEffect(() => {
    if (!setting.isWeekday) {
      db.collection("주일예배").doc('4부').collection(setting.sunday).doc('--stats--').onSnapshot((doc) => {
        const forthtempCount = doc.data().ReservationCount
        setRemainingSunDayFourth(forthtempCount)
      }, (error) => {
        console.log("Error @ListenDB: ", error)
      })
    } 
    return () => {
      console.log('주일예배 4부 리스닝 분리')
    }

  }, [setting.isWeekday, setting.sunday])

  useEffect(() => {
    if (!setting.isWeekday) {
      db.collection("주일예배").doc('6부').collection(setting.sunday).doc('--stats--').onSnapshot((doc) => {
        const sixthtempCount = doc.data().ReservationCount
        setRemainingSunDaySixth(sixthtempCount)
      }, (error) => {
        console.log("Error @ListenDB: ", error)
      })
    } 
    return () => {
      console.log('주일예배 6부 리스닝 분리')
    }
  }, [setting.isWeekday, setting.sunday])

  return (
    <Container>
      <NavBar />
      <Header>화양교회 예배신청 페이지</Header>
      <SubHeader>
        <div>아버지께서는 자기에게 이렇게 </div>
        <div>예배하는 자들을 찾으시느니라</div>
        <div>(요 4:23)</div>
      </SubHeader>

      {setting.commentOverviewLineOne ? (
        <CommentContainer>
          <CommentHeader>
            <NotificationsIcon style={{ marginRight: '0.5rem'}}/>
            <div>공지사항</div>
          </CommentHeader>
          <CommentBody>
            <div style={{textAlign: 'center'}}>{setting.commentOverviewLineOne}</div>
            <div style={{textAlign: 'center', marginTop: '0.25rem'}}>{setting.commentOverviewLineTwo}</div>
            <div style={{textAlign: 'center', marginTop: '0.25rem'}}>{setting.commentOverviewLineThree}</div>
          </CommentBody>
        </CommentContainer>
      ) : (
        <div></div>
      )}
      
      <div style={{marginBottom: '3rem'}}/>

      {setting.isWeekday ? (
        <>
          <Timer 
            title="수요예배" 
            date={new Date(setting.timerWednesDay[0], setting.timerWednesDay[1], setting.timerWednesDay[2], setting.timerWednesDay[3], setting.timerFriDay[4])} 
            settingFunction={setOpenWednesDay}
            forcingClose={setting.closeWednesDay}
          />
          {/* <Timer title="수요예배" date={new Date(2021, 1, 1, 2, 33)} settingFunction={setOpenWednesDay} forcingClose={setting.forcingCloseWed}/> */}
          <WeekdayServiceButton 
            days="수요일"
            title="수요예배" 
            time="7시30분"
            open={openWednesDay}
            forcingClose={setting.closeWednesDay}
            remaining={remainingWednesDay}
            total={setting.wednesDaySeats} 
            linkTo="/wednesday"
          />
          {setting.commentWed? (
            <CommentContainer>
              <CommentHeader>
                <NotificationsIcon style={{ marginRight: '0.5rem'}}/>
                <div>수요예배 공지사항</div>
              </CommentHeader>
              <CommentBody>
                {setting.commentWed}
              </CommentBody>
            </CommentContainer>
          ) : (
            <div></div>
          )}

          <div style={{marginBottom: '3rem'}}/>
    
          <Timer 
            title="금요성령집회" 
            date={new Date(setting.timerFriDay[0], setting.timerFriDay[1], setting.timerFriDay[2], setting.timerFriDay[3], setting.timerFriDay[4])} 
            settingFunction={setOpenFriDay}
            forcingClose={setting.closeFriDay}
          />
          {/* <Timer title="금요성령집회" date={new Date(2021, 1, 1, 2, 35)} settingFunction={setOpenFriDay}/> */}
          <WeekdayServiceButton 
            days="금요일" 
            title="금요성령집회" 
            time="8시00분"
            open={openFriDay}
            forcingClose={setting.closeFriDay}
            remaining={remainingFriDay} 
            total={setting.fridaySeats} 
            linkTo="/friday"
          />
          {setting.commentFri ? (
            <CommentContainer>
              <CommentHeader>
                <NotificationsIcon style={{ marginRight: '0.5rem'}}/>
                <div>금요성령집회 공지사항</div>
              </CommentHeader>
              <CommentBody>
                {setting.commentFri}
              </CommentBody>
            </CommentContainer>
          ) : (
            <div></div>
          )}
        </>
      ) : (
        <>
          <Timer 
            title="주일예배" 
            date={new Date(setting.timerSunDay[0], setting.timerSunDay[1], setting.timerSunDay[2], setting.timerSunDay[3], setting.timerSunDay[4])} 
            settingFunction={setOpenSunDay}
            forcingClose={setting.closeSunDay}
          />  
          {/* <Timer title="주일예배" date={new Date(2021, 1, 5, 2, 53)} settingFunction={setOpenSunDay}/> */}
          <SundayServiceButton 
            open={openSunDay}
            remaining={[ remainingSunDayFirst, remainingSunDaySecond, remainingSunDayThird, remainingSunDayFourth, remainingSunDaySixth ]}
            total={[ setting.sundayOneSeats, setting.sundayTwoSeats,  setting.sundayThreeSeats, setting.sundayFourSeats, setting.sundaySixSeats]}
            forcingClose={setting.closeSunDay}
            commentInBox={setting.commentInBox}
            linkTo="/sunday"
          />
          {setting.commentSunLineOne ? (
            <CommentContainer>
              <CommentHeader>
                <NotificationsIcon style={{ marginRight: '0.5rem'}}/>
                <div>주일예배 공지사항</div>
              </CommentHeader>
              <CommentBody>
                <div style={{textAlign: 'center'}}>{setting.commentSunLineOne}</div>
                <div style={{textAlign: 'center', marginTop: '0.5rem'}}>{setting.commentSunLineTwo}</div>
                <div style={{textAlign: 'center', marginTop: '0.5rem'}}>{setting.commentSunLineThree}</div>
              </CommentBody>
            </CommentContainer>
          ) : (
            <div></div>
          )}
        </>
      )}
      <div style={{marginBottom: '2rem'}}/>
      {/* <Footer /> */}
    </Container>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const Header = styled.div`
  display: flex;
  justify-content: center;
  margin: 2rem 0 0;
  font-size: 2rem;
  font-weight: 700;

`;

const SubHeader = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem 0 2rem;
  font-size: 1rem;
  font-weight: 400;
  flex-wrap: wrap;
  width: 90%;
`;

const CommentContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 90%;
`;

const CommentHeader = styled.div`
  display: flex;
  width: 100%;
  background-color: #F2B49B;
  align-items: center;
  color: #fff;
  padding: 0.5rem;
  box-sizing: border-box;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
`;

const CommentBody = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 0.75rem;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0.25px 1px gray;
  /* border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px; */
  
`;





export default LandingPage;